import React from "react"
import {NavLink} from "react-router-dom";
import Button from "../../../Components/Button";
import Image from "../../../Components/Image";


import correctingIcon from "../../../Components/images/icon-correcting.svg";
import googleIcon from "../../../Components/images/google-icon.svg";
import atIcon from "../../../Components/images/at-icon.svg";


class ProfileForm extends React.Component {
    constructor(props) {
        super(props);
        
        this.state = {
            name: this.props.userName,
            fullName: this.props.userFullName,
            telegram: this.props.userTelegram,
        };

        this.handleNickChange = this.handleNickChange.bind(this);
        this.handleSubmitNick = this.handleSubmitNick.bind(this);


        this.handleFullNameChange = this.handleFullNameChange.bind(this);
        this.handleSubmitFullName = this.handleSubmitFullName.bind(this);


        this.handleTelegramChange = this.handleTelegramChange.bind(this);
        this.handleSubmitTelegram = this.handleSubmitTelegram.bind(this);

    }
/////////////
    handleNickChange(event) {
        console.log("nick name was chenged", event.target.value);
        this.setState({name: event.target.value});
    }
    handleSubmitNick(event) {
        event.preventDefault();
        console.log('–û—Ç–ø—Ä–∞–≤–ª–µ–Ω–Ω–æ–µ –∏–º—è: ' + this.state.name);
    }

////////////////
    handleFullNameChange(event) {
        console.log("FullName was chenged", event.target.value);
        this.setState({fullName: event.target.value});
    }
    handleSubmitFullName(event) {
        event.preventDefault();
        console.log('–û—Ç–ø—Ä–∞–≤–ª–µ–Ω–Ω–æ–µ FullName: ' + this.state.fullName);
    }
/////////////

    handleTelegramChange(event) {
        console.log("Telegram was chenged", event.target.value);
        this.setState({telegram: event.target.value});
    }
    handleSubmitTelegram(event) {
        event.preventDefault();
        console.log('–û—Ç–ø—Ä–∞–≤–ª–µ–Ω–Ω–æ–µ Telegram: ' + this.state.telegram);
    }






    render() {

        function GoogleUser() {
            return (
                <div className="profile_form__top">
                    <Image src={googleIcon} width={34} height={35} alt={"Google icon"} />
                    <div className="profile_form__block">
                        <p className="profile_form__text">–í—ã–ø–æ–ª–Ω–µ–Ω –≤—Ö–æ–¥ —á–µ—Ä–µ–∑ –∞–∫–∫–∞—É–Ω—Ç Google</p>
                        <p className="profile_form__email">{userEmail}</p>
                    </div>
                </div>
            )
        }
        function SimpleUser() {
            return (
                <div className="profile_form__top">
                    <Image src={atIcon} width={34} height={35} alt={"At icon"} />
                    <div className="profile_form__block">
                        <p className="profile_form__text">–í—ã–ø–æ–ª–Ω–µ–Ω –≤—Ö–æ–¥ —á–µ—Ä–µ–∑ –ª–æ–≥–∏–Ω –∏ –ø–∞—Ä–æ–ª—å</p>
                        <p className="profile_form__email">{userEmail}</p>
                    </div>
                </div>
            )
        }

        const userEmail = this.props.userEmail;


        return (

        <div className="profile_form__full">
            {(userEmail.includes('gmail.com')) ? <GoogleUser/> : <SimpleUser />}


            <form className="profile_form"  onSubmit={(event) => this.handleSubmitNick(event)}>
                <input className="profile_form__input"
                       type="text"
                       name="name"
                       value={this.state.name}
                       onChange={(e)=>this.handleNickChange(e)}
                />
                <Button className="profile_form__change" type="submit">
                    <img src={correctingIcon} alt="–∏–∑–º–µ–Ω–∏—Ç—å –Ω–∏–∫"/>
                </Button>
            </form>


            <form className="profile_form"  onSubmit={(event) => this.handleSubmitFullName(event)}>
                <input className="profile_form__input"
                       type="text"
                       name="fullName"
                       value={this.state.fullName}
                       onChange={(e)=>this.handleFullNameChange(e)}
                       />
                <Button className="profile_form__change" type="submit">
                    <img src={correctingIcon} alt="–∏–∑–º–µ–Ω–∏—Ç—å –∏–º—è"/>
                </Button>
            </form>


            <form className="profile_form"  onSubmit={(event) => this.handleSubmitTelegram(event)}>
                <input className="profile_form__input"
                       type="text"
                       name="telegram"
                       value={this.state.telegram}
                       onChange={(e)=>this.handleTelegramChange(e)}
                />
                <Button className="profile_form__change" type="submit">
                    <img src={correctingIcon} alt="–∏–∑–º–µ–Ω–∏—Ç—å Telegram"/>
                </Button>
            </form>

            <NavLink className="btn link_changePass" to={"/change-password/"}>–ò–∑–º–µ–Ω–∏—Ç—å –ø–∞—Ä–æ–ª—å</NavLink>
        </div>


        );
    }
}



export default ProfileForm